// assets/js/modules/safe-mode.js
// UI toggle for Safe Mode (demo mode). When ON, writes are blocked in Store.save().

import { showToast } from '../ui/components.js';

const SAFE_KEY = 'msaas-safe-mode';

function isSafe() {
  return localStorage.getItem(SAFE_KEY) === '1';
}
function setSafe(on) {
  if (on) localStorage.setItem(SAFE_KEY, '1'); else localStorage.removeItem(SAFE_KEY);
  document.documentElement.classList.toggle('safe-mode', !!on);
  // Inform listeners if they care
  const evt = new CustomEvent('safe:changed', { detail: { on: !!on } });
  window.dispatchEvent(evt);
}

function updateButton(btn) {
  const on = isSafe();
  btn?.classList.toggle('btn-outline-danger', on);
  btn?.classList.toggle('btn-outline-secondary', !on);
  btn?.setAttribute('aria-pressed', on ? 'true' : 'false');
  const icon = btn?.querySelector('i');
  if (icon) icon.className = on ? 'bi bi-shield-lock-fill' : 'bi bi-shield-lock';
  const text = btn?.querySelector('.label');
  if (text) text.textContent = on ? 'Safe ON' : 'Safe OFF';
  const badge = document.getElementById('safeBadge');
  if (badge) badge.classList.toggle('d-none', !on);
}

export function initSafeMode() {
  const btn = document.getElementById('btnSafeMode');
  const badge = document.getElementById('safeBadge');
  // initial render
  document.documentElement.classList.toggle('safe-mode', isSafe());
  updateButton(btn);
  if (badge && isSafe()) badge.classList.remove('d-none');

  btn?.addEventListener('click', () => {
    const on = !isSafe();
    setSafe(on);
    updateButton(btn);
    showToast(on ? 'Safe mode ON — writes disabled' : 'Safe mode OFF — writes enabled', on ? 'secondary' : 'primary');
  });
}
