import { Store } from '../core/store.js';
import { on } from '../core/eventBus.js';
import { escapeHtml, timeAgo } from '../ui/components.js';

let offcanvas, threadEl, metaEl, inputEl, formEl, currentDocId;

function hMsg(m) {
  const mine = m.author === 'You';
  return `<div class="d-flex ${mine?'justify-content-end':''}">
    <div class="msg ${mine?'me':'other'} p-2 mb-2" style="max-width: 80%">
      <div class="small text-body-secondary mb-1">${escapeHtml(m.author)} · ${timeAgo(m.ts)}</div>
      <div>${escapeHtml(m.text)}</div>
    </div>
  </div>`;
}

function renderThread() {
  const msgs = Store.commentsFor(currentDocId);
  threadEl.innerHTML = msgs.map(hMsg).join('') || `<div class="text-body-secondary">No comments yet. Start the discussion!</div>`;
  threadEl.scrollTop = threadEl.scrollHeight;
}

function openFor(docId) {
  currentDocId = docId;
  const d = Store.byId(docId);
  metaEl.innerHTML = `<strong>${escapeHtml(d.title)}</strong> · ${d.tags.map(t=>`#${escapeHtml(t)}`).join(' ')}`;
  renderThread();
  const el = document.getElementById('commentsPanel');
  offcanvas = window.bootstrap.Offcanvas.getInstance(el) || new window.bootstrap.Offcanvas(el);
  offcanvas.show();
}

function onSubmit(e) {
  e.preventDefault();
  const text = inputEl.value.trim();
  if (!text) return;
  Store.addComment(currentDocId, text);
  inputEl.value = '';
  renderThread();
}

export function initComments() {
  threadEl = document.getElementById('thread');
  metaEl = document.getElementById('thread-meta');
  inputEl = document.getElementById('commentInput');
  formEl = document.getElementById('commentForm');
  formEl.addEventListener('submit', onSubmit);

  on('comments:open', ({ docId }) => openFor(docId));
  on('comments:changed', ({ docId }) => { if (docId === currentDocId) renderThread(); });
}
