import { emit } from './eventBus.js';

const KEY = 'msaas-docs-state-v1';


const SAFE_KEY = 'msaas-safe-mode';
const now = () => new Date().toISOString();
const uid = () => (crypto?.randomUUID ? crypto.randomUUID() : Math.random().toString(36).slice(2));

const initial = () => ({
  docs: [
    { id: uid(), title: 'Product Vision 2025', description: 'North-star doc covering strategy, themes, and bets.', tags: ['strategy','vision'], url: '', createdAt: now(), updatedAt: now(), bookmarked: true, starred: true },
    { id: uid(), title: 'Onboarding Guide', description: 'How we ship fast, safely: workflows, rituals, tools.', tags: ['onboarding','playbook'], url: '', createdAt: now(), updatedAt: now(), bookmarked: false, starred: false },
    { id: uid(), title: 'API Reference', description: 'Public endpoints, quotas, examples and gotchas.', tags: ['api','docs'], url: '', createdAt: now(), updatedAt: now(), bookmarked: true, starred: false },
    { id: uid(), title: 'Security Review Template', description: 'Checklist for new features before launch.', tags: ['security','template'], url: '', createdAt: now(), updatedAt: now(), bookmarked: false, starred: false }
  ],
  comments: {},
  activities: [],
  savedFilters: []
});

function load() {
  try {
    const raw = localStorage.getItem(KEY);
    if (!raw) return initial();
    const parsed = JSON.parse(raw);
    if (!parsed.savedFilters) parsed.savedFilters = [];
    if (!parsed.activities) parsed.activities = [];
    if (!parsed.comments) parsed.comments = {};
    if (!parsed.docs) parsed.docs = [];
    return parsed;
  } catch {
    return initial();
  }
}

let state = load();

function save() {
  if (localStorage.getItem(SAFE_KEY) === '1') { return; }
  localStorage.setItem(KEY, JSON.stringify(state));
}

function activity(type, meta) {
  const entry = { id: uid(), type, ts: now(), meta };
  state.activities.unshift(entry);
  emit('activity:added', entry);
}

export const Store = {
  all() { return [...state.docs].sort((a,b) => new Date(b.updatedAt) - new Date(a.updatedAt)); },
  byId(id) { return state.docs.find(d => d.id === id); },

  search(query) {
    const q = query.trim().toLowerCase();
    if (!q) return this.all();
    return this.all().filter(d =>
      d.title.toLowerCase().includes(q) ||
      d.description.toLowerCase().includes(q) ||
      (d.tags||[]).some(t => t.toLowerCase().includes(q))
    );
  },

  getAllTags() {
    const s = new Set();
    for (const d of state.docs) for (const t of (d.tags||[])) s.add(t);
    return Array.from(s).sort((a,b)=>a.localeCompare(b));
  },

  addDoc({ title, description, tags = [], url = '' }) {
    const doc = { id: uid(), title, description, tags, url, createdAt: now(), updatedAt: now(), bookmarked: false, starred: false };
    state.docs.unshift(doc);
    save();
    emit('docs:changed');
    activity('doc:new', { id: doc.id, title: doc.title });
    return doc;
  },

  toggleBookmark(id) {
    const d = this.byId(id); if (!d) return;
    d.bookmarked = !d.bookmarked; d.updatedAt = now();
    save();
    emit('docs:changed');
    activity(d.bookmarked ? 'doc:bookmarked' : 'doc:unbookmarked', { id, title: d.title });
  },

  toggleStar(id) {
    const d = this.byId(id); if (!d) return;
    d.starred = !d.starred; d.updatedAt = now();
    save();
    emit('docs:changed');
    activity(d.starred ? 'doc:starred' : 'doc:unstarred', { id, title: d.title });
  },

  updateTags(id, tags = []) {
    const d = this.byId(id); if (!d) return;
    d.tags = tags; d.updatedAt = now();
    save();
    emit('docs:changed');
    activity('doc:tags', { id, title: d.title, tags });
  },

  
  updateTitle(id, title = '') {
    const d = this.byId(id); if (!d) return;
    const t = String(title || '').trim(); if (!t) return;
    d.title = t; d.updatedAt = now();
    save(); emit('docs:changed'); activity('doc:title', { id, title: d.title });
  },

  updateDoc(id, patch = {}) {
    const d = this.byId(id); if (!d) return;
    if (patch.title       !== undefined) d.title       = String(patch.title || '').trim();
    if (patch.description !== undefined) d.description = String(patch.description || '').trim();
    if (patch.tags        !== undefined) d.tags        = Array.isArray(patch.tags) ? patch.tags : [];
    if (patch.url         !== undefined) d.url         = String(patch.url || '').trim();
    if (patch.owner       !== undefined) d.owner       = String(patch.owner || '').trim();
    if (patch.status      !== undefined) d.status      = String(patch.status || '').trim();
    if (patch.icon        !== undefined) d.icon        = String(patch.icon || '').trim();
    if (patch.color       !== undefined) d.color       = String(patch.color || '').trim();
    d.updatedAt = now();
    save(); emit('docs:changed'); activity('doc:update', { id, title: d.title, tags: d.tags });
  },
remove(id) {
    const d = this.byId(id); if (!d) return;
    state.docs = state.docs.filter(x => x.id !== id);
    delete state.comments[id];
    save();
    emit('docs:changed');
    activity('doc:deleted', { id, title: d.title });
  },

  commentsFor(id) {
    return state.comments[id] ? [...state.comments[id]] : [];
  },

  addComment(id, text, author = 'You') {
    if (!state.comments[id]) state.comments[id] = [];
    const msg = { id: uid(), text, author, ts: now() };
    state.comments[id].push(msg);
    save();
    emit('comments:changed', { docId: id, msg });
    const d = this.byId(id);
    activity('comment:new', { docId: id, title: d?.title || 'Unknown', author });
    return msg;
  },

  activities() { return [...state.activities]; },

  counts() {
    const all = state.docs.length;
    const bookmarks = state.docs.filter(d => d.bookmarked).length;
    const starred = state.docs.filter(d => d.starred).length;
    return { all, bookmarks, starred };
  },

  // Smart filters
  savedFilters() { return [...state.savedFilters]; },
  addFilter({ name, query = '', tags = [] }) {
    const f = { id: uid(), name, query, tags };
    state.savedFilters.push(f);
    save();
    emit('filters:changed');
    return f;
  },
  removeFilter(id) {
    state.savedFilters = state.savedFilters.filter(f => f.id !== id);
    save();
    emit('filters:changed');
  },
  exportFilters() {
    return state.savedFilters.map(({ name, query = '', tags = [] }) => ({ name, query, tags }));
  },
  importFilters(filters = [], { overwriteByName = false } = {}) {
    const arr = Array.isArray(filters) ? filters : JSON.parse(filters);
    let added = 0;
    for (const f of arr) {
      const name = String(f.name||'').trim();
      if (!name) continue;
      const query = String(f.query||'');
      const tags = Array.isArray(f.tags) ? f.tags.map(t=>String(t).trim()).filter(Boolean) : [];
      if (overwriteByName) {
        state.savedFilters = state.savedFilters.filter(x => x.name != name);
      } else if (state.savedFilters.some(x => x.name === name)) {
        continue;
      }
      state.savedFilters.push({ id: uid(), name, query, tags });
      added++;
    }
    save();
    emit('filters:changed');
    return added;
  },
  quickImportStarters() {
    const starters = [
      { name: 'Onboarding', tags: ['onboarding'] },
      { name: 'API', tags: ['api'] },
      { name: 'Security', tags: ['security'] },
      { name: 'Vision & Strategy', tags: ['vision','strategy'] }
    ];
    return this.importFilters(starters);
  },

  // Add inside export const Store = { ... }
  renameFilter(id, newName) {
    const f = state.savedFilters.find(x => x.id === id);
    if (!f) return false;
    const name = String(newName || '').trim();
    if (!name) return false;
    // prevent duplicate names
    if (state.savedFilters.some(x => x.id !== id && x.name === name)) return false;
    f.name = name;
    save();
    emit('filters:changed');
    return true;
  },

  reorderFilters(orderIds = []) {
    const map = new Map(state.savedFilters.map(f => [f.id, f]));
    const next = [];
    for (const id of orderIds) if (map.has(id)) next.push(map.get(id));
    // append any missing (safety)
    for (const f of state.savedFilters) if (!orderIds.includes(f.id)) next.push(f);
    state.savedFilters = next;
    save();
    emit('filters:changed');
  },

  removeFilters(ids = []) {
    const set = new Set(ids);
    state.savedFilters = state.savedFilters.filter(f => !set.has(f.id));
    save();
    emit('filters:changed');
  },
  // --- Tag admin APIs (added) ---
  renameTag(oldName, newName) {
    const from = String(oldName || '').trim();
    const to   = String(newName || '').trim();
    if (!from || !to || from === to) return false;
    let changed = 0;
    state.docs.forEach(d => {
      const before = d.tags || [];
      const next = [];
      let touched = false;
      const seen = new Set();
      for (const t of before) {
        const repl = (t === from) ? to : t;
        if (!seen.has(repl)) next.push(repl);
        seen.add(repl);
        if (repl !== t) touched = true;
      }
      if (touched) { d.tags = next; changed++; }
    });
    state.savedFilters.forEach(f => {
      const before = f.tags || [];
      const next = [];
      let touched = false;
      const seen = new Set();
      for (const t of before) {
        const repl = (t === from) ? to : t;
        if (!seen.has(repl)) next.push(repl);
        seen.add(repl);
        if (repl !== t) touched = true;
      }
      if (touched) { f.tags = next; changed++; }
    });
    save();
    emit('docs:changed'); emit('filters:changed');
    return changed;
  },

  deleteTag(tagName) {
    const name = String(tagName || '').trim();
    if (!name) return false;
    let changed = 0;
    state.docs.forEach(d => {
      const before = d.tags || [];
      const next = before.filter(t => t !== name);
      if (next.length !== before.length) { d.tags = next; changed++; }
    });
    state.savedFilters.forEach(f => {
      const before = f.tags || [];
      const next = before.filter(t => t !== name);
      if (next.length !== before.length) { f.tags = next; changed++; }
    });
    save();
    emit('docs:changed'); emit('filters:changed');
    return changed;
  },

  mergeTags(sourceNames = [], targetName) {
    const target = String(targetName || '').trim();
    if (!target || !Array.isArray(sourceNames) || sourceNames.length === 0) return false;
    const set = new Set(sourceNames.map(s => String(s || '').trim()).filter(Boolean));
    set.delete(target);
    let total = 0;
    for (const s of set) total += this.renameTag(s, target) || 0;
    return total;
  },
  // --- Safe Mode helpers ---
  isSafeMode() { return localStorage.getItem(SAFE_KEY) === '1'; },
  setSafeMode(on) { if (on) localStorage.setItem(SAFE_KEY, '1'); else localStorage.removeItem(SAFE_KEY); },

};
