// assets/js/modules/tags-admin.js
import { Store } from '../core/store.js';
import { showToast } from '../ui/components.js';
let btnOpen, modalRoot, listEl, searchEl, btnRename, btnMerge, btnDelete, targetInput;
function getTagStats(){const counts=new Map();const bump=(name,key)=>{const n=String(name||'').trim();if(!n)return;const v=counts.get(n)||{docs:0,filters:0};v[key]+=1;counts.set(n,v)};const docs=Store.all();for(const d of docs){const uniq=new Set((d.tags||[]).filter(Boolean));for(const t of uniq)bump(t,'docs')}const filters=Store.savedFilters();for(const f of filters){const uniq=new Set((f.tags||[]).filter(Boolean));for(const t of uniq)bump(t,'filters')}return[...counts.entries()].sort((a,b)=>a[0].localeCompare(b[0]))}
function escapeHtml(s=''){return String(s).replace(/[&<>"']/g,(ch)=>({'&':'&amp;','<':'&lt;','>':'&gt;','"':'&quot;',"'":'&#39;'}[ch]))}
function rowTpl(name,meta){return`<tr data-name="${escapeHtml(name)}"><td class="text-center" style="width:36px"><input class="form-check-input tag-check" type="checkbox" aria-label="Select ${escapeHtml(name)}"></td><td><code>#${escapeHtml(name)}</code></td><td class="text-end"><span class="badge bg-light text-body">Docs: ${meta.docs}</span></td><td class="text-end"><span class="badge bg-light text-body">Filters: ${meta.filters}</span></td><td class="text-end" style="width:40px"><button type="button" class="btn btn-sm btn-outline-secondary one-rename" title="Rename…"><i class="bi bi-pencil"></i></button></td></tr>`}
function render(){const q=(searchEl?.value||'').trim().toLowerCase();const rows=getTagStats().filter(([name])=>q?name.toLowerCase().includes(q):true).map(([name,meta])=>rowTpl(name,meta)).join('');listEl.innerHTML=rows||`<tr><td colspan="5" class="text-body-secondary py-4 text-center">No tags found.</td></tr>`}
function selectedTags(){return[...listEl.querySelectorAll('.tag-check:checked')].map(cb=>cb.closest('tr')?.dataset?.name).filter(Boolean)}
function requireSelection(min,exact=null){const sel=selectedTags();if((min&&sel.length<min)||(exact!=null&&sel.length!==exact)){showToast(exact!=null?`Select exactly ${exact} tag${exact===1?'':'s'}`:`Select at least ${min} tag${min===1?'':'s'}`,'warning');return null}return sel}
function doRename(oldName,newName){const n=String(newName||'').trim();if(!n){showToast('Enter a new tag name','warning');return}if(n===oldName){showToast('New name is the same','secondary');return}const res=Store.renameTag?Store.renameTag(oldName,n):null;if(res===false){showToast('Rename failed','danger');return}render();showToast(`Renamed #${oldName} → #${n}`,'primary')}
function doMerge(names,target){const t=String(target||'').trim();if(!t){showToast('Enter a target tag name','warning');return}if(Store.mergeTags){Store.mergeTags(names,t)}else if(Store.renameTag){const uniq=new Set(names);for(const name of uniq)if(name!==t)Store.renameTag(name,t)}else{showToast('Store API missing: renameTag/mergeTags','danger');return}render();showToast(`Merged ${names.length} tag(s) into #${t}`,'primary')}
function doDelete(names){if(!confirm(`Delete ${names.length} tag${names.length===1?'':'s'} from all docs and filters?`))return;if(Store.deleteTag){for(const n of names)Store.deleteTag(n)}else{const docs=Store.all();for(const d of docs){const next=(d.tags||[]).filter(t=>!names.includes(t));if(next.length!==(d.tags||[]).length)Store.updateTags(d.id,next)}}render();showToast('Deleted','secondary')}
function wireModal(){searchEl.addEventListener('input',render);listEl.addEventListener('click',e=>{const btn=e.target.closest('.one-rename');if(!btn)return;const tr=btn.closest('tr');const oldName=tr?.dataset?.name;if(!oldName)return;const val=prompt(`Rename #${oldName} to:`);if(val!=null)doRename(oldName,val)});btnRename.addEventListener('click',()=>{const sel=requireSelection(1,1);if(!sel)return;const val=targetInput.value.trim()||prompt(`Rename #${sel[0]} to:`)||'';if(!val)return;doRename(sel[0],val);targetInput.value=''});btnMerge.addEventListener('click',()=>{const sel=requireSelection(2,null);if(!sel)return;const val=targetInput.value.trim()||prompt('Merge into (target tag):')||'';if(!val)return;doMerge(sel,val);targetInput.value=''});btnDelete.addEventListener('click',()=>{const sel=requireSelection(1,null);if(!sel)return;doDelete(sel)})}
export function initTagsAdmin(){btnOpen=document.getElementById('btnManageTags');modalRoot=document.getElementById('manageTagsModal');listEl=document.getElementById('tags-list');searchEl=document.getElementById('tags-search');btnRename=document.getElementById('tags-rename');btnMerge=document.getElementById('tags-merge');btnDelete=document.getElementById('tags-delete');targetInput=document.getElementById('tags-target');if(!btnOpen||!modalRoot)return;btnOpen.addEventListener('click',()=>{render();const modal=window.bootstrap?.Modal?.getInstance(modalRoot)||new window.bootstrap.Modal(modalRoot);modal.show()});wireModal()}
