import { Store } from '../core/store.js';

export function escapeHtml(s='') { 
  return s.replace(/[&<>"]/g, c => ({'&':'&amp;','<':'&lt;','>':'&gt;','"':'&quot;'}[c]));
}

export function tagBadges(tags=[]) {
  return (tags||[]).map(t => `
    <button type="button" class="badge badge-elevated me-1 tag-badge tag-badge-link" 
            data-action="filter-tag" data-tag="${escapeHtml(t)}" title="Filter by #${escapeHtml(t)}">#${escapeHtml(t)}</button>`
  ).join('');
}

function statusCls(s='') { s=(s||'').toLowerCase();
  if(s.includes('publish')) return 'text-success-emphasis bg-success-subtle';
  if(s.includes('review'))  return 'text-warning-emphasis bg-warning-subtle';
  if(s.includes('draft'))   return 'text-secondary-emphasis bg-secondary-subtle';
  if(s.includes('archive')) return 'text-body-secondary bg-body-secondary-subtle';
  return 'text-secondary-emphasis bg-secondary-subtle';
}

export function timeAgo(iso) {
  const d = new Date(iso); 
  const s = Math.floor((Date.now()-d)/1000);
  const r = (n,u) => `${n} ${u}${n>1?'s':''} ago`;
  if (s<60) return r(s,'sec');
  const m=Math.floor(s/60); if(m<60) return r(m,'min');
  const h=Math.floor(m/60); if(h<24) return r(h,'hr');
  const dd=Math.floor(h/24); if(dd<30) return r(dd,'day');
  const mo=Math.floor(dd/30); if(mo<12) return r(mo,'mo');
  const y=Math.floor(mo/12); return r(y,'yr');
}

export function docCard(doc) {
  const urlBtn = doc.url ? `<a href="${doc.url}" target="_blank" rel="noopener" class="btn btn-outline-secondary"><i class="bi bi-box-arrow-up-right"></i></a>` : '';
  return `
  <div class="col-12 col-sm-6 col-xl-4" data-id="${doc.id}">
    <div class="card doc h-100" ${doc.color?`style="border-left:4px solid ${escapeHtml(doc.color)}"`:""}>
      <div class="card-body d-flex flex-column">
        <div class="d-flex justify-content-between align-items-start mb-2">
          <h5 class="card-title me-2">${escapeHtml(doc.title)}</h5>
          <div class="btn-group doc-actions" role="group">
            ${urlBtn}
            <button class="btn btn-outline-secondary" data-action="comment" title="Comments"><i class="bi bi-chat"></i></button>
            <button class="btn ${doc.bookmarked?'btn-secondary':'btn-outline-secondary'}" data-action="bookmark" title="Bookmark"><i class="bi bi-bookmark${doc.bookmarked?'-fill':''}"></i></button>
            <button class="btn ${doc.starred?'btn-warning':'btn-outline-warning'}" data-action="star" title="Star"><i class="bi bi-star${doc.starred?'-fill':''}"></i></button>
            ${(window.Auth?.canEdit?.() ? `<button class="btn btn-outline-secondary" data-action="edit-doc" title="Edit"><i class="bi bi-pencil-square"></i></button>` : ``)}
            <button class="btn btn-outline-secondary" data-action="edit-tags" title="Edit tags"><i class="bi bi-tags"></i></button>
            <button class="btn btn-outline-danger" data-action="delete" title="Delete"><i class="bi bi-trash"></i></button>
          </div>
        </div>
        ${(doc.owner||doc.status||doc.icon||doc.color)?`
        <div class="doc-meta d-flex align-items-center gap-2 mb-2 text-body-secondary">
          ${doc.icon?`<span class="doc-icon">${escapeHtml(doc.icon)}</span>`:""}
          ${doc.color?`<span class="doc-color-dot" style="background:${escapeHtml(doc.color)}"></span>`:""}
          ${doc.owner?`<span class="doc-owner">By ${escapeHtml(doc.owner)}</span>`:""}
          ${doc.status?`<button type="button" class="badge rounded-pill ms-auto ${statusCls(doc.status)}" data-action="filter-status" data-status="${escapeHtml(doc.status)}" title="Filter by status">${escapeHtml(doc.status)}</button>`:""}
        </div>`:``}
        <p class="card-text flex-grow-1">${escapeHtml(doc.description)}</p>

        <div class="d-flex justify-content-between align-items-center">
          <div class="d-flex flex-wrap gap-1" style="max-width:60%">${tagBadges(doc.tags)}</div>
          <small class="text-body-secondary">Updated ${timeAgo(doc.updatedAt)}</small>
        </div>

        <div class="tag-editor d-none mt-2">
          <div class="input-group input-group-sm">
            <input type="text" class="form-control" placeholder="tag1, tag2" value="${escapeHtml((doc.tags||[]).join(', '))}" list="all-tags">
            <button class="btn btn-primary" data-action="save-tags">Save</button>
            <button class="btn btn-outline-secondary" data-action="cancel-tags">Cancel</button>
          </div>
          <div class="form-text">Separate tags with commas.</div>
        </div>

      </div>
    </div>
  </div>`;
}

