export function showToast(message, variant='primary') {
  const holder = document.getElementById('toast-holder');
  const el = document.createElement('div');
  el.className = `toast align-items-center text-bg-${variant} border-0`;
  el.role = 'status'; el.ariaLive = 'polite'; el.ariaAtomic = 'true';
  el.innerHTML = `
    <div class="d-flex">
      <div class="toast-body">${message}</div>
      <button type="button" class="btn-close btn-close-white me-2 m-auto" data-bs-dismiss="toast" aria-label="Close"></button>
    </div>`;
  holder.appendChild(el);
  const t = new window.bootstrap.Toast(el, { delay: 2200 });
  t.show();
  el.addEventListener('hidden.bs.toast', () => el.remove());
}

export function updateCounts() {
  const c = Store.counts();
  document.getElementById('count-all').textContent = c.all;
  document.getElementById('count-bookmarks').textContent = c.bookmarks;
  document.getElementById('count-starred').textContent = c.starred;
}
