// assets/js/modules/shortcuts.js
import { Store } from '../core/store.js';
import { showToast } from '../ui/components.js';
let activeId=null,lastActiveEl=null;
function setActiveCard(el){if(lastActiveEl)lastActiveEl.classList.remove('kbd-active');lastActiveEl=el;if(el){el.classList.add('kbd-active');activeId=el.getAttribute('data-id')||el.getAttribute('data-doc-id')}else{activeId=null}}
function getActiveId(){if(activeId)return activeId;const el=document.querySelector('.doc-card,[data-id],[data-doc-id]');if(el)setActiveCard(el.closest('[data-id],[data-doc-id]')||el);return activeId}
function toggleStar(){const id=getActiveId();if(!id)return showToast('Select a card first (click it)','secondary');Store.toggleStar?.(id);showToast('Toggled star','primary')}
function toggleBookmark(){const id=getActiveId();if(!id)return showToast('Select a card first (click it)','secondary');Store.toggleBookmark?.(id);showToast('Toggled bookmark','primary')}
function editTagsQuick(){const id=getActiveId();if(!id)return showToast('Select a card first (click it)','secondary');const el=lastActiveEl;const btn=el?.querySelector?.('.btn-edit-tags,[data-action="edit-tags"]');if(btn){btn.click();return}const doc=Store.byId?.(id);const current=(doc?.tags||[]).join(', ');const val=prompt('Edit tags (comma-separated):',current);if(val==null)return;const tags=val.split(',').map(s=>s.trim()).filter(Boolean);Store.updateTags?.(id,tags);showToast('Tags updated','primary')}
function focusSearch(){const ids=['inputSearch','search','q'];for(const id of ids){const el=document.getElementById(id);if(el){el.focus();el.select?.();return}}const el=document.querySelector('input[type="search"], input[name*="search" i]');if(el){el.focus();el.select?.()}}
function toggleCheatsheet(){const modalEl=document.getElementById('shortcutsModal');if(!modalEl)return;const modal=window.bootstrap?.Modal?.getInstance(modalEl)||new window.bootstrap.Modal(modalEl);if(modalEl.classList.contains('show'))modal.hide();else modal.show()}
function isTypingInField(e){const t=e.target;const tag=(t.tagName||'').toLowerCase();const editable=t.isContentEditable||t.getAttribute?.('contenteditable')==='true';return editable||tag==='input'||tag==='textarea'||tag==='select'}
function handleKeydown(e){if(isTypingInField(e))return;if((e.key==='/'&&!e.shiftKey)||(e.key==='k'&&e.ctrlKey)){e.preventDefault();focusSearch();return}if(e.key==='?'||(e.key.toLowerCase()==='k'&&e.metaKey)){e.preventDefault();toggleCheatsheet();return}if(e.key.toLowerCase()==='s'&&!e.ctrlKey&&!e.metaKey&&!e.altKey){e.preventDefault();toggleStar();return}if(e.key.toLowerCase()==='b'&&!e.ctrlKey&&!e.metaKey&&!e.altKey){e.preventDefault();toggleBookmark();return}if(e.key.toLowerCase()==='e'&&!e.ctrlKey&&!e.metaKey&&!e.altKey){e.preventDefault();editTagsQuick();return}}
function trackActiveCard(){document.addEventListener('click',e=>{const card=e.target.closest?.('[data-id],[data-doc-id],.doc-card');if(card)setActiveCard(card.closest?.('[data-id],[data-doc-id]')||card)})}
export function initShortcuts(){trackActiveCard();document.addEventListener('keydown',handleKeydown);const first=document.querySelector('[data-id],[data-doc-id],.doc-card');if(first)setActiveCard(first.closest?.('[data-id],[data-doc-id]')||first)}
