// assets/js/core/auth.js
const ROLE_KEY = 'msaas-user-role';
export const Auth = {
  getRole() { return localStorage.getItem(ROLE_KEY) || 'admin'; },
  setRole(role='viewer') { localStorage.setItem(ROLE_KEY, role); },
  isAdmin() { return this.getRole() === 'admin'; },
  canEdit() { return this.isAdmin(); }
};
window.Auth = Auth;