// assets/js/modules/admin-sidebar.js
export function initAdminSidebar() {
  function doAdminSidebarInit() {
    const toggleBtn = document.getElementById('btnAdminPanel');
    const panelEl = document.getElementById('adminPanel');
    if (!toggleBtn || !panelEl) return;
    document.querySelectorAll('#adminAccordion').forEach(el => {
      if (!panelEl.contains(el)) el.remove();
    });
    document.querySelectorAll('.accordion-item').forEach(item => {
      const ids = ['sec-create-wrap', 'sec-safe-wrap', 'sec-filters-wrap', 'sec-tags-wrap', 'sec-data-wrap', 'sec-utils-wrap'];
      const hasAdminSection = ids.some(id => item.querySelector('#' + id));
      if (hasAdminSection && !panelEl.contains(item)) item.remove();
    });
    const offcanvas = window.bootstrap?.Offcanvas ? (window.bootstrap.Offcanvas.getOrCreateInstance(panelEl)) : null;
    toggleBtn.addEventListener('click', () => offcanvas && offcanvas.toggle());
    
    function reLocateTools() {
      const move = (id, containerId) => { const el = document.getElementById(id); const slot = document.getElementById(containerId); if (el && slot) { el.classList.remove('btn-sm'); el.classList.add('btn', 'btn-outline-secondary', 'w-100'); mark(el); slot.appendChild(el); } };
      const moveInline = (id, containerId) => { const el = document.getElementById(id); const slot = document.getElementById(containerId); if (el && slot) { mark(el); slot.appendChild(el); } };
      moveInline('safeBadge', 'sec-safe'); move('btnSafeMode', 'sec-safe');
      moveInline('qaForm', 'sec-create');
      move('btnQuickImport', 'sec-filters'); move('btnImportFilters', 'sec-filters'); move('btnExportFilters', 'sec-filters'); move('btnManageFilters', 'sec-filters');
      move('btnManageTags', 'sec-tags'); move('btnBulkSelect', 'sec-tags');
      move('btnBackupExport', 'sec-data'); move('btnBackupImport', 'sec-data');
      move('btnShortcuts', 'sec-utils');
    }
    
    const mark = (el) => { el?.classList?.add('admin-tool'); if (!el.dataset.adminLabel) el.dataset.adminLabel = (el.innerText || '').trim().toLowerCase(); };
    const move = (id, containerId) => { const el = document.getElementById(id); const slot = document.getElementById(containerId); if (el && slot) { el.classList.remove('btn-sm'); el.classList.add('btn', 'btn-outline-secondary', 'w-100'); mark(el); slot.appendChild(el); } };
    const moveInline = (id, containerId) => { const el = document.getElementById(id); const slot = document.getElementById(containerId); if (el && slot) { mark(el); slot.appendChild(el); } };
    reLocateTools();
    const search = document.getElementById('adminSearch');
    function applyFilter(q) { const query = (q || '').trim().toLowerCase(); const secs = panelEl.querySelectorAll('.accordion-item'); secs.forEach(item => { const body = item.querySelector('.accordion-body'); const tools = body ? body.querySelectorAll('.admin-tool, form.quick-add') : []; let vis = 0; tools.forEach(el => { const label = el.dataset.adminLabel || (el.innerText || '').toLowerCase(); const show = !query || label.includes(query); el.classList.toggle('d-none', !show); if (show) vis++; }); item.classList.toggle('d-none', vis === 0); }); }
    search?.addEventListener('input', () => applyFilter(search.value));
    panelEl.addEventListener('shown.bs.offcanvas', () => { if (search) { search.value = ''; applyFilter(''); search.focus(); } reLocateTools(); });
  }
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', doAdminSidebarInit, { once: true });
  } else {
    doAdminSidebarInit();
  }
}
