// assets/js/modules/bulk-actions.js
// Bulk select + batch actions (star, bookmark, add/remove tag).
import { Store } from '../core/store.js';
import { showToast } from '../ui/components.js';

let mode = false;
const selected = new Set();
let bar, countEl, btnSelect, btnClear, btnStar, btnUnstar, btnBm, btnUnbm, btnAddTag, btnRemoveTag;

function cardSelector() {
  return '[data-id],[data-doc-id],.doc-card';
}

function getId(el) {
  const card = el.closest?.(cardSelector());
  if (!card) return null;
  return card.getAttribute('data-id') || card.getAttribute('data-doc-id') || null;
}

function setSelectedVisual(id, on) {
  const card = document.querySelector(`${cardSelector()}[data-id="${id}"]`) ||
               document.querySelector(`${cardSelector()}[data-doc-id="${id}"]`);
  if (card) {
    card.classList.toggle('bulk-selected', on);
    card.setAttribute('aria-selected', on ? 'true' : 'false');
  }
}

function refreshCount() {
  if (countEl) countEl.textContent = String(selected.size);
  if (bar) bar.classList.toggle('d-none', !mode);
}

function toggleMode(on) {
  mode = (on === undefined) ? !mode : !!on;
  document.body.classList.toggle('bulk-mode', mode);
  if (!mode) {
    for (const id of Array.from(selected)) setSelectedVisual(id, false);
    selected.clear();
  }
  refreshCount();
}

function toggleSelFromEvent(e) {
  const id = getId(e.target);
  if (!id) return;
  if (!mode) return; // ignore if not in bulk mode
  e.preventDefault();
  e.stopPropagation();
  if (selected.has(id)) {
    selected.delete(id);
    setSelectedVisual(id, false);
  } else {
    selected.add(id);
    setSelectedVisual(id, true);
  }
  refreshCount();
}

function ensureSomethingSelected() {
  if (selected.size === 0) {
    showToast('No items selected', 'secondary');
    return false;
  }
  return true;
}

function bulkStar(shouldStar) {
  if (!ensureSomethingSelected()) return;
  let changed = 0;
  for (const id of selected) {
    const d = Store.byId?.(id);
    if (!d) continue;
    const want = !!shouldStar;
    if ((d.starred || false) !== want) {
      Store.toggleStar?.(id);
      changed++;
    }
  }
  showToast(`${shouldStar ? 'Starred' : 'Unstarred'} ${changed} item(s)`, 'primary');
}

function bulkBookmark(shouldBm) {
  if (!ensureSomethingSelected()) return;
  let changed = 0;
  for (const id of selected) {
    const d = Store.byId?.(id);
    if (!d) continue;
    const want = !!shouldBm;
    if ((d.bookmarked || false) !== want) {
      Store.toggleBookmark?.(id);
      changed++;
    }
  }
  showToast(`${shouldBm ? 'Bookmarked' : 'Unbookmarked'} ${changed} item(s)`, 'primary');
}

function promptTag(title, initial='') {
  const val = prompt(title, initial);
  if (val == null) return null;
  const tag = String(val).trim();
  if (!tag) { showToast('Tag cannot be empty', 'warning'); return null; }
  return tag;
}

function bulkAddTag() {
  if (!ensureSomethingSelected()) return;
  const tag = promptTag('Add tag to selected:', '');
  if (tag == null) return;
  let changed = 0;
  for (const id of selected) {
    const d = Store.byId?.(id);
    if (!d) continue;
    const tags = Array.from(new Set([...(d.tags || []), tag]));
    if (tags.length !== (d.tags || []).length) {
      Store.updateTags?.(id, tags);
      changed++;
    }
  }
  showToast(`Added #${tag} to ${changed} item(s)`, 'primary');
}

function bulkRemoveTag() {
  if (!ensureSomethingSelected()) return;
  const tag = promptTag('Remove which tag from selected?', '');
  if (tag == null) return;
  let changed = 0;
  for (const id of selected) {
    const d = Store.byId?.(id);
    if (!d) continue;
    const before = d.tags || [];
    const next = before.filter(t => t !== tag);
    if (next.length !== before.length) {
      Store.updateTags?.(id, next);
      changed++;
    }
  }
  showToast(`Removed #${tag} from ${changed} item(s)`, 'secondary');
}

function wireBarButtons() {
  btnClear?.addEventListener('click', () => toggleMode(false));
  btnStar?.addEventListener('click', () => bulkStar(true));
  btnUnstar?.addEventListener('click', () => bulkStar(false));
  btnBm?.addEventListener('click', () => bulkBookmark(true));
  btnUnbm?.addEventListener('click', () => bulkBookmark(false));
  btnAddTag?.addEventListener('click', bulkAddTag);
  btnRemoveTag?.addEventListener('click', bulkRemoveTag);
}

export function initBulkActions() {
  bar = document.getElementById('bulkBar');
  countEl = document.getElementById('bulkCount');
  btnSelect = document.getElementById('btnBulkSelect');
  btnClear = document.getElementById('bulkClear');
  btnStar = document.getElementById('bulkStar');
  btnUnstar = document.getElementById('bulkUnstar');
  btnBm = document.getElementById('bulkBookmark');
  btnUnbm = document.getElementById('bulkUnbookmark');
  btnAddTag = document.getElementById('bulkAddTag');
  btnRemoveTag = document.getElementById('bulkRemoveTag');

  // Button to enter/exit select mode
  btnSelect?.addEventListener('click', () => {
    toggleMode(!mode);
  });

  // Listen on document for card clicks in bulk mode
  document.addEventListener('click', (e) => {
    if (!mode) return;
    if (e.target.closest?.('#bulkBar')) return; // ignore clicks inside bar
    toggleSelFromEvent(e);
  });

  wireBarButtons();
  refreshCount();
}
