import { Store } from '../core/store.js';
import { on } from '../core/eventBus.js';
import { docCard2 } from '../ui/components.js';

let grid;

function render() {
  const list = Store.all().filter(d => d.bookmarked);
  grid.innerHTML = list.map(docCard2).join('') || empty('No bookmarks yet.');
}

function empty(text) {
  return `<div class="col-12 text-center text-body-secondary py-5">
    <i class="bi bi-bookmark" style="font-size:3rem"></i>
    <div class="mt-2">${text}</div>
  </div>`;
}

export function initBookmarks() {
  grid = document.getElementById('grid-bookmarks');
  render();
  on('docs:changed', render);
}
