// assets/js/modules/admin-extensions.js
import { showToast } from '../ui/components.js';
import { Auth } from '../core/auth.js';

function findSelectedIds() {
  // support multiple possible markers
  const set = new Set();
  document.querySelectorAll('[data-selected="1"],[data-selected="true"],.selected,.is-selected').forEach(el=>{
    const id = el.getAttribute('data-id') || el.getAttribute('data-doc-id');
    if (id) set.add(id);
  });
  // fallback: Bulk module might maintain a list on window
  if (window.Bulk && Array.isArray(window.Bulk.getSelected?.())) {
    window.Bulk.getSelected().forEach(id => set.add(id));
  }
  return Array.from(set);
}



export function initAdminExtensions(){
  const THEME_KEY = 'msaas-theme';
  window.Theme = window.Theme || {};

  function applyTheme(val){
    const root = document.documentElement;
    root.classList.remove('theme-dark','theme-light');
    if (val === 'dark')  root.classList.add('theme-dark');
    if (val === 'light') root.classList.add('theme-light');
  }

  function updateThemeUI(mode){
    const cs  = getComputedStyle(document.documentElement);
    const map = {
      auto:  (cs.getPropertyValue('--indicator-auto')  || '').trim() || '#16a34a',
      light: (cs.getPropertyValue('--indicator-light') || '').trim() || '#f59e0b',
      dark:  (cs.getPropertyValue('--indicator-dark')  || '').trim() || '#3b82f6'
    };
    ['btnThemeQuick','btnPalette'].forEach(id => {
      const b = document.getElementById(id); if (!b) return;
      b.title = `Theme: ${mode} (click to cycle)`;
      let dot = b.querySelector('.theme-indicator');
      if (!dot) { dot = document.createElement('span'); dot.className = 'theme-indicator'; b.appendChild(dot); }
      dot.style.background = map[mode] || map.auto;
    });
  }

  window.Theme.get   = () => localStorage.getItem(THEME_KEY) || 'auto';
  window.Theme.set   = (v) => { localStorage.setItem(THEME_KEY, v); applyTheme(v); updateThemeUI(v); };
  window.Theme.cycle = () => {
    const order = ['auto','light','dark'];
    const cur = window.Theme.get();
    const next = order[(order.indexOf(cur)+1) % order.length];
    window.Theme.set(next);
    try { if (window.showToast) showToast('Theme: ' + next, 'secondary'); } catch(e){}
  };

  const savedTheme = window.Theme.get();
  applyTheme(savedTheme);
  updateThemeUI(savedTheme);

  const btnQuick   = document.getElementById('btnThemeQuick');
  if (btnQuick)   btnQuick.addEventListener('click', () => window.Theme.cycle());
  const btnPalette = document.getElementById('btnPalette');
  if (btnPalette) btnPalette.addEventListener('click', () => window.Theme.cycle());

  const rbAuto  = document.getElementById('themeAuto');
  const rbLight = document.getElementById('themeLight');
  const rbDark  = document.getElementById('themeDark');
  if (rbAuto && rbLight && rbDark) {
    ({auto:rbAuto, light:rbLight, dark:rbDark}[savedTheme] || rbAuto).checked = true;
    [rbAuto, rbLight, rbDark].forEach(rb => rb.addEventListener('change', (e) => {
      if (e.target.checked) window.Theme.set(e.target.value);
    }));
  }
}
