import { Store } from '../core/store.js';
import { on, emit } from '../core/eventBus.js';
import { docCard, showToast, updateCounts } from '../ui/components.js';

let gridAll, searchInput, activeTagsEl, filtersBarEl, savedFiltersEl, btnSaveFilter, addDocButton;
let btnQuickImport;
const selectedTags = new Set();

function emptyState(text) {
  return `
  <div class="col-12">
    <div class="text-center text-body-secondary py-5">
      <i class="bi bi-folder2-open" style="font-size:3rem"></i>
      <div class="mt-2">${text}</div>
    </div>
  </div>`;
}

function applyFilters(list) {
  const textFiltered = list;
  if (selectedTags.size === 0) return textFiltered;
  return textFiltered.filter(d => {
    const statusTokens = Array.from(selectedTags).filter(x => String(x).toLowerCase().startsWith('status:')).map(x => String(x).toLowerCase().slice(7));
    const tags = new Set((d.tags||[]).map(t => t.toLowerCase()));
    for (const t of selectedTags) {
      const tl = String(t).toLowerCase();
      if (tl.startsWith('status:')) continue; // handled below
      if (!tags.has(tl)) return false;
    }
    if (statusTokens.length) {
      const ds = String(d.status||'').toLowerCase();
      if (!statusTokens.includes(ds)) return false;
    }
    return true;
  });
}

function render(list) {
  const base = searchInput.value ? Store.search(searchInput.value) : Store.all();
  const final = applyFilters(base);
  gridAll.innerHTML = final.length ? final.map(docCard).join('') : emptyState('No documents match your filters.');
  updateCounts();
  renderActiveFilters();
}


// local HTML escape for chip labels
function escapeHtml(s=''){
  return String(s)
    .replaceAll('&','&amp;')
    .replaceAll('<','&lt;')
    .replaceAll('>','&gt;')
    .replaceAll('"','&quot;')
    .replaceAll("'",'&#39;');
}

function prettyChipLabel(token=''){

  const s = String(token);
  if (s.toLowerCase().startsWith('status:')) {
    const v = s.slice(7);
    // Capitalize first letter only, keep rest as is if user typed
    const label = v ? (v.charAt(0).toUpperCase() + v.slice(1)) : '';
    return `Status: ${escapeHtml(label)}`;
  }
  return `#${escapeHtml(s)}`;
}

function statusIcon(status=''){
  const s = String(status || '').toLowerCase();
  // Simple dot using Bootstrap Icons (falls back to emoji circle if BI not present)
  const dot = (hex) => `<i class="bi bi-circle-fill me-1" style="font-size:.6rem;color:${hex}"></i>`;
  if (s.includes('publish')) return dot('#16a34a'); // green
  if (s.includes('review'))  return dot('#f59e0b'); // amber
  if (s.includes('draft'))   return dot('#6c757d'); // gray
  if (s.includes('archive')) return dot('#64748b'); // slate
  return dot('#6c757d');
}

function renderActiveFilters() {
  if (selectedTags.size === 0) {
    filtersBarEl.classList.add('d-none');
    activeTagsEl.innerHTML = '';
  } else {
    filtersBarEl.classList.remove('d-none');
    const chips = Array.from(selectedTags).map(t => `
      <span class="filter-chip" data-chip="${t}">${(String(t).toLowerCase().startsWith("status:")? statusIcon(String(t).slice(7)) : "")}${prettyChipLabel(t)}
        <button class="remove" type="button" data-action="remove-chip" aria-label="Remove tag ${t}"><i class="bi bi-x-lg"></i></button>
      </span>`).join('');
    const clear = `<button type="button" class="btn btn-sm btn-outline-secondary filter-clear" data-action="clear-chips"><i class="bi bi-x-circle"></i> Clear</button>`;
    activeTagsEl.innerHTML = chips + clear;
  }
  renderSavedFilters();
}


function renderSavedFilters() {
  const items = Store.savedFilters();
  savedFiltersEl.innerHTML = items.length ? items.map(f => `
    <span class="smart-chip d-inline-flex flex-column align-items-start" data-id="${f.id}">
      <div class="w-100 d-flex align-items-center gap-1">
        <button class="apply" type="button" data-action="apply-saved" title="Apply filter">${f.name}</button>
        <button class="remove" type="button" data-action="remove-saved" title="Remove saved filter ${f.name}"><i class="bi bi-x-circle"></i></button>
      </div>
      ${Array.isArray(f.tags) && f.tags.length ? `
        <div class="saved-preview small mt-1">
          ${f.tags.map(t => `<span class="badge bg-body-tertiary border me-1">${(String(t).toLowerCase().startsWith('status:')? statusIcon(String(t).slice(7)) : '')}${prettyChipLabel(t)}</span>`).join('')}
        </div>` : ``}
    </span>`).join('') : '<span class="text-body-secondary small">No saved filters yet.</span>';
}

function toggleEditor(card, show=true) {
  const ed = card.querySelector('.tag-editor');
  if (!ed) return;
  ed.classList.toggle('d-none', !show);
  if (show) ed.querySelector('input')?.focus();
}

function handleGridClicks(e) {
  const btn = e.target.closest('button');
  if (!btn) return;
  const wrap = e.target.closest('[data-id]');
  const id = wrap?.dataset?.id; if (!id) return;
  const action = btn.dataset.action;
  
  if (action === 'bookmark') { Store.toggleBookmark(id); showToast('Bookmark toggled','secondary'); }
  if (action === 'star') { Store.toggleStar(id); showToast('Star toggled','warning'); }
  if (action === 'delete') {
    const title = wrap.querySelector('.doc-title,.card-title')?.textContent?.trim();
    const msg = `Delete ${title ? `"${title}"` : 'this document'}?\nThis action cannot be undone.`;
    niceConfirm(msg, { okText: 'Delete', cancelText: 'Cancel', variant: 'danger' }).then((ok) => {
      if (!ok) return; // user canceled
      if (typeof Store.deleteDoc === 'function') Store.deleteDoc(id, { confirmed: true });
      else if (typeof Store.remove === 'function') Store.remove(id);
      else if (typeof Store.del === 'function') Store.del(id);
      showToast('Document deleted', 'danger');
      render(Store.all());
    });
    return; // prevent fall-through render when awaiting confirm
  }
  if (action === 'comment') { emit('comments:open', { docId: id }); }

  if (action === 'filter-status') {
    const s = (btn.dataset.status||'').toLowerCase();
    const tok = 'status:'+s;
    if (selectedTags.has(tok)) selectedTags.delete(tok); else selectedTags.add(tok);
    render(Store.all());
    return;
  }

  if (action === 'filter-tag') {
    const tag = btn.dataset.tag || '';
    if (selectedTags.has(tag)) selectedTags.delete(tag); else selectedTags.add(tag);
    render(Store.all());
    return;
  }

  if (action === 'edit-doc') { openEditDoc(id); return; }

  if (action === 'edit-tags') {
    const card = btn.closest('.card');
    const open = card.querySelector('.tag-editor')?.classList.contains('d-none') === false;
    gridAll.querySelectorAll('.tag-editor').forEach(el => el.classList.add('d-none'));
    toggleEditor(card, !open);
    return;
  }

  if (action === 'save-tags') {
    const card = btn.closest('.card');
    const input = card.querySelector('.tag-editor input');
    const tags = (input?.value || '').split(',').map(s=>s.trim()).filter(Boolean);
    Store.updateTags(id, tags);
    showToast('Tags updated','primary');
    render(Store.all());
    return;
  }

  if (action === 'cancel-tags') {
    const card = btn.closest('.card');
    toggleEditor(card, false);
    return;
  }

  render(Store.all());
}

function handleFiltersBarClick(e) {
  const rm = e.target.closest('[data-action="remove-chip"]');
  const clr = e.target.closest('[data-action="clear-chips"]');
  const applySaved = e.target.closest('[data-action="apply-saved"]');
  const removeSaved = e.target.closest('[data-action="remove-saved"]');

  if (rm) {
    const chip = rm.closest('[data-chip]')?.dataset?.chip;
    if (chip) selectedTags.delete(chip);
    render(Store.all());
    return;
  }
  if (clr) { selectedTags.clear(); render(Store.all()); return; }

  if (applySaved) {
    const id = applySaved.closest('[data-id]')?.dataset?.id;
    const f = Store.savedFilters().find(x => x.id === id);
    if (f) {
      selectedTags.clear();
      for (const t of (f.tags||[])) selectedTags.add(t);
      searchInput.value = f.query || '';
      render(Store.all());
    }
    return;
  }
  if (removeSaved) {
    const id = removeSaved.closest('[data-id]')?.dataset?.id;
    Store.removeFilter(id);
    renderSavedFilters();
    showToast('Saved filter removed','secondary');
  }
}

function openEditDoc(id) {
  const safeSet = (form, name, val) => { const el = form?.querySelector(`[name="${name}"]`); if (el) el.value = val ?? ''; };
  const d = Store.byId(id); if (!d) return;
  const form = document.getElementById('editDocForm');
  form.dataset.mode = 'edit';
  form.dataset.id = id;
  form.querySelector('[name="title"]').value = d.title || '';
  form.querySelector('[name="description"]').value = d.description || '';
  form.querySelector('[name="tags"]').value = (d.tags||[]).join(', ');
  safeSet(form,'url', d.url || '');
  safeSet(form,'owner', d.owner || '');
  safeSet(form,'status', d.status || '');
  safeSet(form,'icon', d.icon || '');
  safeSet(form,'color', d.color || '#2563eb');

  const modalEl = document.getElementById('editDocModal');
  const titleEl = modalEl.querySelector('.modal-title');
  const submitBtn = form.querySelector('button[type="submit"]');
  if (titleEl) titleEl.textContent = 'Edit Document';
  if (submitBtn) submitBtn.textContent = 'Save';

  // Remove inert when showing
  modalEl.addEventListener('show.bs.modal', function handlerShow() {
    modalEl.removeAttribute('inert');
    modalEl.removeEventListener('show.bs.modal', handlerShow);
  });

  const modal = window.bootstrap.Modal.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
  modal.show();
}

function handleAddDocModal() {
  openModalById('addDocModal');
}

function wireAddDocForm() {
  const modalEl = document.getElementById('addDocModal');
  const form = document.getElementById('addDocForm');

  form.addEventListener('submit', (e) => {
    e.preventDefault();
    const fd = new FormData(form);
    const title = fd.get('title')?.toString().trim();
    const description = fd.get('description')?.toString().trim();
    const tags = (fd.get('tags')?.toString()||'').split(',').map(s=>s.trim()).filter(Boolean);
    const url = fd.get('url')?.toString().trim();
    const owner = fd.get('owner')?.toString().trim() || '';
    const status = fd.get('status')?.toString().trim() || '';
    const icon = fd.get('icon')?.toString().trim() || '';
    const color = fd.get('color')?.toString().trim() || '';
    if (!title || !description) return;

    Store.addDoc({ title, description, tags, url, owner, status, icon, color });
    showToast(`Added “${title}”`,'primary');

    form.reset();
    form.dataset.mode='';
    form.dataset.id='';
    const modal = window.bootstrap.Modal.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
    modal.hide();
    
    render(Store.all());
  });
}

function wireEditDocForm() {
  const form = document.getElementById('editDocForm');

  form.addEventListener('submit', (e) => {
    e.preventDefault();
    const fd = new FormData(form);
    const title = fd.get('title')?.toString().trim();
    const description = fd.get('description')?.toString().trim();
    const tags = (fd.get('tags')?.toString()||'').split(',').map(s=>s.trim()).filter(Boolean);
    const url = fd.get('url')?.toString().trim();
    const owner = fd.get('owner')?.toString().trim() || '';
    const status = fd.get('status')?.toString().trim() || '';
    const icon = fd.get('icon')?.toString().trim() || '';
    const color = fd.get('color')?.toString().trim() || '';
    if (!title || !description) return;

    const id = form.dataset.id;
    Store.updateDoc(id, { title, description, tags, url, owner, status, icon, color });
    showToast('Saved changes','primary');

    form.reset();
    form.dataset.mode='';
    form.dataset.id='';
    const modalEl = document.getElementById('editDocModal');
    const modal = window.bootstrap.Modal.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
    modal.hide();

    render(Store.all());
  });
}

function wireSaveFilter() {
  btnSaveFilter.addEventListener('click', () => {
    const modalEl = document.getElementById('saveFilterModal');
    const modal = window.bootstrap.Modal.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
    document.getElementById('saveFilterName').value = '';
    modal.show();
  });

  const form = document.getElementById('saveFilterForm');
  form.addEventListener('submit', (e) => {
    e.preventDefault();
    const name = document.getElementById('saveFilterName').value.trim();
    const query = searchInput.value.trim();
    const tags = Array.from(selectedTags);
    if (!name) return;
    if (!query && tags.length === 0) { showToast('Nothing to save — add text or tag filters first','warning'); return; }
    Store.addFilter({ name, query, tags });
    showToast('Saved filter','primary');
    renderSavedFilters();
    const modalEl = document.getElementById('saveFilterModal');
    const modal = window.bootstrap.Modal.getInstance(modalEl);
    modal.hide();
  });

  on('filters:changed', renderSavedFilters);
}

function wireImportExport() {
  btnQuickImport.addEventListener('click', () => {
    const added = Store.quickImportStarters();
    renderSavedFilters();
    showToast(`${added} starter filter${added===1?'':'s'} added`, added? 'primary':'secondary');
  });

  const form = document.getElementById('importFiltersForm');
  form.addEventListener('submit', (e) => {
    e.preventDefault();
    try {
      const raw = document.getElementById('importFiltersJson').value.trim();
      const added = Store.importFilters(JSON.parse(raw));
      showToast(`${added} filter${added===1?'':'s'} imported`, 'primary');
      renderSavedFilters();
      const modalEl = document.getElementById('importFiltersModal');
      const modal = window.bootstrap.Modal.getInstance(modalEl);
      modal.hide();
    } catch (err) {
      showToast('Invalid JSON', 'danger');
    }
  });
}

function openModalById(id) {
  const el = document.getElementById(id);
  if (!el) return;
  // Ensure correct ARIA before showing
  el.removeAttribute('aria-hidden');
  el.removeAttribute('inert');
  el.setAttribute('aria-modal', 'true');

  // JS options override data-bs-focus="false"
  let inst = bootstrap.Modal.getInstance(el);
  if (!inst) inst = new bootstrap.Modal(el, { focus: true, backdrop: true, keyboard: true });
  inst.show();
}

export function initDocuments() {
  gridAll = document.getElementById('grid-all');
  searchInput = document.getElementById('searchInput');
  activeTagsEl = document.getElementById('active-tags');
  filtersBarEl = document.getElementById('filtersBar');
  savedFiltersEl = document.getElementById('saved-filters');
  btnSaveFilter = document.getElementById('btnSaveFilter');
  btnQuickImport = document.getElementById('btnQuickImport');
  addDocButton = document.getElementById('addDocButton');

  gridAll.addEventListener('click', handleGridClicks);
  activeTagsEl.addEventListener('click', handleFiltersBarClick);
  savedFiltersEl.addEventListener('click', handleFiltersBarClick);
  addDocButton.addEventListener('click', handleAddDocModal);

  wireAddDocForm();
  wireEditDocForm();
  wireSaveFilter();
  wireImportExport();

  render(Store.all());

  searchInput.addEventListener('input', () => render(Store.search(searchInput.value)));

  on('docs:changed', () => { render(Store.all()); });
  document.addEventListener('force:rerender', () => { render(Store.all()); });
}
