// assets/js/modules/backup.js
import { showToast } from '../ui/components.js';

const KEY = 'msaas-docs-state-v1';

function readState() {
  try {
    const raw = localStorage.getItem(KEY);
    return raw ? JSON.parse(raw) : { docs: [], comments: {}, activities: [], savedFilters: [] };
  } catch (e) {
    return { docs: [], comments: {}, activities: [], savedFilters: [] };
  }
}

function validateState(obj) {
  const errors = [];
  const ok = (cond, msg) => { if (!cond) errors.push(msg); };

  ok(obj && typeof obj === 'object' && !Array.isArray(obj), 'Root must be an object');
  ok(Array.isArray(obj.docs), '`docs` must be an array');
  ok(obj.comments && typeof obj.comments === 'object' && !Array.isArray(obj.comments), '`comments` must be an object');
  ok(Array.isArray(obj.activities), '`activities` must be an array');
  ok(Array.isArray(obj.savedFilters), '`savedFilters` must be an array');

  return { valid: errors.length === 0, errors };
}

function download(filename, text) {
  const blob = new Blob([text], { type: 'application/json' });
  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url; a.download = filename;
  document.body.appendChild(a);
  a.click();
  a.remove();
  URL.revokeObjectURL(url);
}

function ts() {
  const d = new Date();
  const pad = (n) => String(n).padStart(2, '0');
  return d.getFullYear()
    + '' + pad(d.getMonth() + 1)
    + '' + pad(d.getDate())
    + '-' + pad(d.getHours()) + pad(d.getMinutes()) + pad(d.getSeconds());
}

export function initBackupUI() {
  const btnExport = document.getElementById('btnBackupExport');
  const btnImport = document.getElementById('btnBackupImport');
  const modalEl   = document.getElementById('importBackupModal');
  const fileEl    = document.getElementById('importBackupFile');
  const textEl    = document.getElementById('importBackupJson');
  const overwriteEl = document.getElementById('importBackupOverwrite');
  const form      = document.getElementById('importBackupForm');

  if (btnExport) {
    btnExport.addEventListener('click', () => {
      const state = readState();
      const pretty = JSON.stringify(state, null, 2);
      download('dochub-backup-' + ts() + '.json', pretty);
      showToast('Backup downloaded', 'primary');
    });
  }

  if (btnImport) {
    btnImport.addEventListener('click', () => {
      if (!modalEl) return;
      const modal = window.bootstrap?.Modal?.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
      if (fileEl) fileEl.value = '';
      if (textEl) textEl.value = '';
      if (overwriteEl) overwriteEl.checked = true;
      modal.show();
    });
  }

  async function loadFileToTextarea(file) {
    if (!file || !textEl) return;
    const txt = await file.text();
    try {
      const parsed = JSON.parse(txt);
      textEl.value = JSON.stringify(parsed, null, 2);
    } catch (e) {
      textEl.value = txt; // let user fix
    }
  }

  if (fileEl) {
    fileEl.addEventListener('change', async () => {
      const f = fileEl.files && fileEl.files[0];
      if (f) await loadFileToTextarea(f);
    });
  }

  if (form) {
    form.addEventListener('submit', (e) => {
      e.preventDefault();
      try {
        const raw = (textEl?.value || '').trim();
        const parsed = JSON.parse(raw);
        const v = validateState(parsed);
        if (!v.valid) {
          showToast('Import failed: ' + v.errors.join('; '), 'danger');
          return;
        }
        if (overwriteEl?.checked) {
          localStorage.setItem(KEY, JSON.stringify(parsed));
        } else {
          // shallow merge
          const cur = readState();
          const merged = {
            docs: [...(cur.docs || []), ...(parsed.docs || [])],
            comments: { ...(cur.comments || {}), ...(parsed.comments || {}) },
            activities: [...(cur.activities || []), ...(parsed.activities || [])],
            savedFilters: [...(cur.savedFilters || []), ...(parsed.savedFilters || [])]
          };
          localStorage.setItem(KEY, JSON.stringify(merged));
        }
        showToast('Import complete — reloading…', 'primary');
        setTimeout(() => window.location.reload(), 400);
      } catch (err) {
        showToast('Invalid JSON', 'danger');
      }
    });
  }
}
