// assets/js/modules/filters-admin.js
import { Store } from '../core/store.js';
import { showToast } from '../ui/components.js';

let listEl, selectAllEl, btnSave, btnDelete, btnOpen;

function rowTpl(f) {
  const tags = (f.tags || []).join(', ');
  return `
    <li class="list-group-item p-0" draggable="true" data-id="${f.id}">
      <div class="mf-row">
        <span class="mf-handle bi bi-grip-vertical" title="Drag to reorder"></span>
        <input class="form-check-input mf-check" type="checkbox" aria-label="Select ${f.name}">
        <input class="form-control form-control-sm mf-input mf-name" value="${escapeHtml(f.name)}" />
        <div class="small text-body-secondary mf-tags">${escapeHtml(tags)}</div>
        <button type="button" class="btn btn-sm btn-outline-danger mf-del-one" title="Delete"><i class="bi bi-trash"></i></button>
      </div>
    </li>`;
}

function escapeHtml(s='') {
  return String(s)
    .replaceAll('&','&amp;')
    .replaceAll('<','&lt;')
    .replaceAll('>','&gt;')
    .replaceAll('"','&quot;')
    .replaceAll("'","&#39;");
}

function render() {
  const items = Store.savedFilters();
  listEl.innerHTML = items.length ? items.map(rowTpl).join('') :
    `<li class="list-group-item text-body-secondary">No saved filters.</li>`;
}

function initDrag() {
  let draggingEl = null;

  listEl.addEventListener('dragstart', (e) => {
    draggingEl = e.target.closest('[draggable="true"]');
    e.dataTransfer.effectAllowed = 'move';
  });

  listEl.addEventListener('dragover', (e) => {
    const over = e.target.closest('[draggable="true"]');
    if (!draggingEl || !over || over === draggingEl) return;
    e.preventDefault();
    // visual hint
    over.classList.add('drag-over');
    // position insert
    const rect = over.getBoundingClientRect();
    const before = (e.clientY - rect.top) < rect.height / 2;
    listEl.insertBefore(draggingEl, before ? over : over.nextSibling);
  });

  listEl.addEventListener('dragleave', (e) => {
    e.target.closest('[draggable="true"]')?.classList.remove('drag-over');
  });

  listEl.addEventListener('drop', (e) => {
    e.preventDefault();
    listEl.querySelectorAll('.drag-over').forEach(el => el.classList.remove('drag-over'));
    draggingEl = null;
  });
}

function idsFromDOM() {
  return [...listEl.querySelectorAll('[draggable="true"]')].map(li => li.dataset.id);
}

function validateNames() {
  const inputs = [...listEl.querySelectorAll('.mf-input')];
  let ok = true;
  const seen = new Set();
  for (const inp of inputs) {
    const name = inp.value.trim();
    inp.classList.remove('is-invalid');
    if (!name) { inp.classList.add('is-invalid'); ok = false; }
    if (seen.has(name)) { inp.classList.add('is-invalid'); ok = false; }
    seen.add(name);
  }
  return ok;
}

function saveChanges() {
  if (!validateNames()) { showToast('Fix invalid/duplicate names', 'danger'); return; }

  // rename pass
  const rows = [...listEl.querySelectorAll('[draggable="true"]')];
  for (const li of rows) {
    const id = li.dataset.id;
    const name = li.querySelector('.mf-name').value.trim();
    if (!Store.renameFilter(id, name)) {
      li.querySelector('.mf-name').classList.add('is-invalid');
      showToast(`Could not rename “${name}”`, 'danger');
      return;
    }
  }
  // reorder pass
  Store.reorderFilters(idsFromDOM());

  showToast('Filters updated', 'primary');
  render(); // reflect sanitized state
}

function bulkDelete() {
  const ids = [...listEl.querySelectorAll('.mf-check:checked')].map(c => c.closest('[draggable="true"]').dataset.id);
  if (ids.length === 0) { showToast('No filters selected', 'secondary'); return; }
  if (!confirm(`Delete ${ids.length} filter${ids.length===1?'':'s'}? This cannot be undone.`)) return;
  Store.removeFilters(ids);
  showToast('Deleted', 'secondary');
  render();
}

function listHandlers() {
  // per-row delete
  listEl.addEventListener('click', (e) => {
    const btn = e.target.closest('.mf-del-one');
    if (!btn) return;
    const id = btn.closest('[draggable="true"]').dataset.id;
    Store.removeFilters([id]);
    render();
  });

  // select all
  selectAllEl.addEventListener('change', () => {
    listEl.querySelectorAll('.mf-check').forEach(c => c.checked = selectAllEl.checked);
  });
}

export function initFiltersAdmin() {
  btnOpen     = document.getElementById('btnManageFilters');
  listEl      = document.getElementById('mf-list');
  selectAllEl = document.getElementById('mf-select-all');
  btnSave     = document.getElementById('mf-save');
  btnDelete   = document.getElementById('mf-delete');

  if (!btnOpen || !listEl) return; // modal not present

  btnOpen.addEventListener('click', () => {
    render();
    const modalEl = document.getElementById('manageFiltersModal');
    const modal = window.bootstrap.Modal.getInstance(modalEl) || new window.bootstrap.Modal(modalEl);
    modal.show();
  });

  btnSave.addEventListener('click', saveChanges);
  btnDelete.addEventListener('click', bulkDelete);

  listHandlers();
  initDrag();
}
