
// assets/js/modules/quick-add.js
// Compact "Add doc" form (title + tags) that calls Store.addDoc and optionally Store.updateTags.
// Safe: tries multiple call signatures and falls back gracefully.

import { Store } from '../core/store.js';
import { showToast } from '../ui/components.js';

function parseTags(raw='') {
  return String(raw)
    .split(',')
    .map(s => s.trim())
    .filter(Boolean);
}

function createDocViaStore(title, tags) {
  const t = String(title || '').trim();
  if (!t) return null;

  let newId = null;
  const tryFns = [
    () => Store.addDoc?.({ title: t, tags }),       // common: object payload
    () => Store.addDoc?.(t, { tags }),              // fallback: (title, opts)
    () => Store.addDoc?.(t)                         // fallback: (title)
  ];

  for (const f of tryFns) {
    try {
      const r = f();
      if (r !== undefined && r !== null) { newId = r; break; }
    } catch (e) { /* ignore and try next */ }
  }

  // If Store.addDoc returns nothing, try to locate by title (latest)
  if (!newId && Store.all) {
    const all = Store.all() || [];
    for (let i = all.length - 1; i >= 0; i--) {
      if (all[i]?.title === t) { newId = all[i].id; break; }
    }
  }

  // Apply tags if we have an id and tags provided
  if (newId && tags?.length && Store.updateTags) {
    try {
      const d = Store.byId?.(newId);
      const before = d?.tags || [];
      const next = Array.from(new Set([ ...before, ...tags ]));
      if (next.length !== before.length) Store.updateTags(newId, next);
    } catch (e) { /* non-fatal */ }
  }

  return newId;
}

export function initQuickAdd() {
  const form = document.getElementById('qaForm');
  const inputTitle = document.getElementById('qaTitle');
  const inputTags  = document.getElementById('qaTags');
  const btnClear   = document.getElementById('qaClear');

  if (!form || !inputTitle || !inputTags) return;

  // Submit creates the doc
  form.addEventListener('submit', (e) => {
    e.preventDefault();
    const title = inputTitle.value.trim();
    const tags = parseTags(inputTags.value);
    if (!title) {
      showToast('Please enter a title', 'warning');
      inputTitle.focus();
      return;
    }
    const id = createDocViaStore(title, tags);
    if (id) {
      showToast('Document added', 'primary');
      inputTitle.value = '';
      inputTags.value = '';
      inputTitle.focus();
    } else {
      showToast('Could not add the document (check Store.addDoc)', 'danger');
    }
  });

  // Clear button
  btnClear?.addEventListener('click', () => {
    inputTitle.value = '';
    inputTags.value = '';
    inputTitle.focus();
  });
}
