function initTheme() {
  const key = 'msaas-theme';
  const html = document.documentElement;
  const btn = document.getElementById('btnTheme');
  const apply = (mode) => {
    html.setAttribute('data-bs-theme', mode);
    btn.innerHTML = `<i class="bi ${mode==='dark'?'bi-moon-stars':'bi-sun'}"></i>`;
    localStorage.setItem(key, mode);
  };
  const stored = localStorage.getItem(key) || 'light';
  apply(stored);
  btn.addEventListener('click', () => apply(html.getAttribute('data-bs-theme')==='light'?'dark':'light'));
}

function rememberActiveTab() {
  const key = 'msaas-tab';
  const tabs = document.getElementById('hubTabs');
  tabs.addEventListener('shown.bs.tab', (e) => localStorage.setItem(key, e.target.id));
  const saved = localStorage.getItem(key);
  if (saved) {
    const trigger = document.getElementById(saved);
    if (trigger) new window.bootstrap.Tab(trigger).show();
  }
}

function initDensity() {
  const key = 'msaas-density';
  const btn = document.getElementById('btnDensity');
  const html = document.body;
  const apply = (mode) => {
    html.classList.toggle('density-compact', mode === 'compact');
    btn.innerHTML = `<i class="bi ${mode==='compact'?'bi-arrows-collapse':'bi-arrows-expand'}"></i>`;
    btn.setAttribute('title', mode==='compact' ? 'Compact density' : 'Comfy density');
    localStorage.setItem(key, mode);
  };
  const stored = localStorage.getItem(key) || 'comfy';
  apply(stored);
  btn.addEventListener('click', () => apply(html.classList.contains('density-compact') ? 'comfy' : 'compact'));
}

function initPalette() {
  const key = 'msaas-brand';
  const btn = document.getElementById('btnPalette');
  const html = document.documentElement;
  const palettes = [ 'default', 'emerald', 'amber', 'ruby', 'indigo' ];
  const label = (p) => p === 'default' ? 'Default palette' : p[0].toUpperCase() + p.slice(1);
  const apply = (p) => {
    if (p === 'default') html.removeAttribute('data-brand');
    else html.setAttribute('data-brand', p);
    btn.setAttribute('title', label(p));
    btn.innerHTML = `<i class="bi bi-palette2"></i>`;
    localStorage.setItem(key, p);
  };
  let current = localStorage.getItem(key) || 'default';
  apply(current);
  btn.addEventListener('click', () => {
    const idx = palettes.indexOf(current);
    current = palettes[(idx + 1) % palettes.length];
    apply(current);
    // refresh tooltips in case title changed
    if (window.bootstrap?.Tooltip) {
      const t = window.bootstrap.Tooltip.getInstance(btn) || new window.bootstrap.Tooltip(btn);
      t.setContent({ '.tooltip-inner': label(current) });
    }
  });
}

function initTooltips() {
  document.querySelectorAll('[data-bs-toggle="tooltip"]').forEach(el => new window.bootstrap.Tooltip(el));
}

// Keep counts consistent across views
function wireCounts() {
  on('docs:changed', updateCounts);
  updateCounts();
}

function openModalById(id) {
  const el = document.getElementById(id);
  if (!el) return;
  // Ensure correct ARIA before showing
  el.removeAttribute('aria-hidden');
  el.removeAttribute('inert');
  el.setAttribute('aria-modal', 'true');

  // JS options override data-bs-focus="false"
  let inst = bootstrap.Modal.getInstance(el);
  if (!inst) inst = new bootstrap.Modal(el, { focus: true, backdrop: true, keyboard: true });
  inst.show();
}

function safeHideModal(modalId, onHidden) {
  const el = document.getElementById(modalId);
  if (!el) { if (typeof onHidden === 'function') onHidden(); return; }
  // Blur any focused element inside the modal to avoid a11y warnings
  if (el.contains(document.activeElement)) {
    try { document.activeElement.blur(); } catch (_) { }
  }
  const inst = bootstrap.Modal.getInstance(el) || new bootstrap.Modal(el);
  // Ensure focus cannot remain in a hidden modal
  el.addEventListener('hidden.bs.modal', function handler() {
    el.setAttribute('inert', '');
    el.removeEventListener('hidden.bs.modal', handler);
    if (typeof onHidden === 'function') onHidden();
  });
  // Remove inert when showing
  el.addEventListener('show.bs.modal', function handlerShow() {
    el.removeAttribute('inert');
    el.removeEventListener('show.bs.modal', handlerShow);
  });
  inst.hide();
}
  
