import { Store } from '../core/store.js';
import { on } from '../core/eventBus.js';
import { escapeHtml, timeAgo } from '../ui/components.js';

let listEl;

const ICON = {
  'doc:new': 'bi-file-earmark-plus',
  'doc:deleted': 'bi-trash',
  'doc:starred': 'bi-star-fill',
  'doc:unstarred': 'bi-star',
  'doc:bookmarked': 'bi-bookmark-fill',
  'doc:unbookmarked': 'bi-bookmark',
  'comment:new': 'bi-chat-dots',
  'doc:tags': 'bi-tags'
};

function item(a) {
  const i = ICON[a.type] || 'bi-activity';
  const title = escapeHtml(a.meta?.title || '');
  const who = escapeHtml(a.meta?.author || '');
  let text = '';
  if (a.type === 'comment:new') text = `${who} commented on <strong>${title}</strong>`;
  if (a.type === 'doc:new') text = `New document <strong>${title}</strong>`;
  if (a.type === 'doc:deleted') text = `Deleted <strong>${title}</strong>`;
  if (a.type === 'doc:starred') text = `Starred <strong>${title}</strong>`;
  if (a.type === 'doc:unstarred') text = `Unstarred <strong>${title}</strong>`;
  if (a.type === 'doc:bookmarked') text = `Bookmarked <strong>${title}</strong>`;
  if (a.type === 'doc:unbookmarked') text = `Removed bookmark on <strong>${title}</strong>`;
  if (a.type === 'doc:tags') text = `Updated tags on <strong>${title}</strong>`;

  return `<div class="d-flex align-items-start gap-3">
    <div class="rounded-circle bg-body-secondary d-flex align-items-center justify-content-center" style="width:38px;height:38px">
      <i class="bi ${i}"></i>
    </div>
    <div class="flex-grow-1">
      <div>${text}</div>
      <div class="small text-body-secondary">${timeAgo(a.ts)}</div>
    </div>
  </div>`;
}

function render() {
  const feed = Store.activities();
  listEl.innerHTML = feed.map(item).join('') || `<div class="text-body-secondary">No activity yet.</div>`;
}

export function initCommunity() {
  listEl = document.getElementById('list-feed');
  render();
  on('activity:added', render);
}
